.testimonial {
  position: relative;
  padding-top: 100px;
  padding-bottom: 100px;

  &::before {
    content: "";
    position: absolute;
    background-image: url(../../images/geometric-bg.png);
    background-repeat: no-repeat;
    background-size: contain;
    width: 50%;
    height: 105%;
    top: 0;
    left: -215px;
    z-index: -1;
  }

  &::after {
    content: "";
    position: absolute;
    background-image: url(../../images/geometric-bg.png);
    transform: scale3d(-1, 1, -1);
    background-repeat: no-repeat;
    background-size: contain;
    width: 50%;
    height: 105%;
    top: 0;
    right: -215px;
    z-index: -1;
  }

  .title-area {
    text-align: center;
    width: 885px;
    max-width: 100%;
    margin: 0 auto 40px;

    h2 {
      font-weight: bold;
      font-size: 39.81px;
      line-height: 50px;
      text-align: center;
      letter-spacing: -1px;
      color: #595959;
      margin-bottom: 15px;
    }

    h3 {
      font-weight: normal;
      font-size: 23.04px;
      line-height: 37px;
      text-align: center;
      letter-spacing: -1px;
      color: #595959;
    }
  }

  .testimonial-area {
    display: none;
    width: 885px;
    max-width: 100%;
    margin: 0 auto;
    background: #213C48;
    padding: 67px 40px 105px;

    &.slick-initialized {
      display: block;
    }

    .slick-prev {
      left: calc(50% - 33px);
      bottom: 33px;
      top: unset;
      width: 29px;
      height: 29px;
      border: 1px solid #909DA3;
      display: flex;
      align-items: center;
      justify-content: center;

      &::before {
        content: '<';
        opacity: .5;
        font-family: 'Poppins', sans-serif;
        font-weight: 600;
        font-size: 33px;
        line-height: 30px;
        text-align: center;
        color: #fff;
        transition: all .35s;
      }

      &:hover {
        &::before {
          opacity: .8;
          transition: all .35s;
        }
      }
    }

    .slick-next {
      right: calc(50% - 35px);
      bottom: 33px;
      top: unset;
      width: 29px;
      height: 29px;
      border: 1px solid #909DA3;
      display: flex;
      align-items: center;
      justify-content: center;

      &::before {
        content: '>';
        opacity: .5;
        font-family: 'Poppins', sans-serif;
        font-weight: 600;
        font-size: 35px;
        line-height: 30px;
        text-align: center;
        color: #fff;
        transition: all .35s;
      }

      &:hover {
        &::before {
          opacity: .8;
          transition: all .35s;
        }
      }
    }

    .testimonial-box {
      text-align: center;
      padding: 0 18%;
      max-width: 100%;
      margin: 0 auto;
      position: relative;

      &::before {
        content: "";
        font-weight: 700;
        font-size: 39.81px;
        line-height: 50px;
        letter-spacing: -1px;
        color: #00b287;
        position: absolute;
        left: 13%;
        top: 50%;
        transform: translateY(-50%);
      }

      &::after {
        content: "";
        font-weight: bold;
        font-size: 39.81px;
        line-height: 50px;
        letter-spacing: -1px;
        color: #00B287;
        position: absolute;
        right: 13%;
        top: 50%;
        transform: translateY(-50%);
      }

      h4 {
        font-weight: 600;
        font-size: 16px;
        line-height: 26px;
        text-align: center;
        color: #00B287;
        margin-bottom: 18px;
      }

      h3 {
        font-style: italic;
        font-weight: 400;
        font-size: 19.2px;
        line-height: 31px;
        text-align: center;
        letter-spacing: -1px;
        color: #fff;
      }
    }

  }
}

